*, :before, :after {
  box-sizing: border-box;
}

.body {
  background: var(--black);
  color: var(--white);
  font-family: Roboto, sans-serif;
}

:root {
  --black: #111;
  --gray-dark: #282828;
  --gray: #595959;
  --gray-light: #b7b7b7;
  --white: #fff;
  --silver: #f8f8f8;
  --orange: #f87719;
  --gradient-1: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --gradient-2: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
}

::selection {
  background-color: var(--gray);
  color: var(--silver);
}

img {
  height: auto;
  width: 100%;
  display: block;
}

html {
  scroll-behavior: smooth;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

ul {
  padding: 0;
  list-style: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

a {
