}

.container {
  width: 100%;
  margin: auto;
  padding: 0 20px;
}

.hero-library {
  background-image: linear-gradient(79.39deg, #111 32.37%, rgba(17, 17, 17, 0) 72.02%), url("hero-bg-library.dca58190.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.hero-box {
