  font-weight: 400;
  line-height: 16px;
}

.dropdown-library {
  margin: 40px 0 16px;
  display: inline-block;
  position: relative;
}

.dropdown-toggle-library {
  width: 173px;
  cursor: pointer;
  background-color: var(--black);
  border: 1px solid var(--white);
  border-radius: 8px;
  padding: 10px 56px 10px 16px;
  font-size: 14px;
  line-height: 16px;
}

.dropdown-menu-library {
  width: 172px;
  background-color: var(--black);
  border: 1px solid var(--white);
  z-index: 5000;
  border-radius: 8px;
  display: none;
  position: absolute;
}

.dropdown-item-library {
  cursor: pointer;
  border-bottom: 1px solid var(--white);
  width: 100%;
  padding: 14px 66px 10px 20px;
  font-size: 14px;
  line-height: 20px;
}

.dropdown-item-library:hover, .dropdown-item-library:active {
  color: var(--orange);
}

.card-library {
  width: 280px;
  height: 406px;
  cursor: pointer;
  border-radius: 5px;
  position: relative;
}

.cards-library-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  margin: 0;
  display: flex;
}

.card-inner-library {
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  padding: 160px 12px 12px;
  position: absolute;
  bottom: 0;
}

.card-title-inner-library {
  text-transform: uppercase;
  color: var(--white);
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
}

.card-descr-inner-library {
  color: var(--gray-light);
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
}

.card-start-inner-library {
  width: 106px;
