  align-items: center;
  gap: 4px;
  display: flex;
}

.card-descr-inner-library-box {
  justify-content: space-between;
  display: flex;
}

.icon-card-start-inner-library {
  fill: var(--orange);
  height: 14px;
}

.cards-btn-library {
  background-image: var(--gradient-1);
  color: var(--black);
  border: var(--gradient-1);
  border-radius: 74px;
  margin: 40px 0 16px;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
}

.cards-btn-library:hover {
  color: var(--orange);
  background-color: var(--white);
  border: var(--gradient-1);
  background-image: none;
}

.search-more {
  background-image: var(--gradient-1);
  color: var(--black);
  border: var(--gradient-1);
  border-radius: 74px;
  margin: 40px 0 16px;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
}

.search-more:hover {
  color: var(--orange);
  background-color: var(--white);
  border: var(--gradient-1);
  background-image: none;
}

.dropdown-item-library:last-child {
  border: none;
}

.notify {
  text-align: center;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
}

.empty-gallery {
  text-align: center;
  margin: 80px auto 20px;
}

.load-more-library {
  text-align: center;
  margin: auto;
}

.dropdown-toggle-library:before {
  content: "";
  width: 16px;
  height: 16px;
  opacity: 10;
  fill: var(--white);
  background-image: url("chevron-down.168da858.svg");
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: 13px;
  right: 14px;
}

.dropdown-toggle-library.rotated:before {
  transform: rotate(180deg);
}

.dropdown-item-library.\.dropdown-item-library-color {
  color: var(--orange);
}

